<template>
    <div v-if="ready">
        <div class='flex col-12'>
            <div class="col-6">
                <!--Swap Filter-->
                <div class="card">
                    <div class="card-header">
                        <strong>Swap Filter Setting(s)</strong>
                    </div>    
                    <div class="card-body">
                        <div class="row">
                            <div class="col-8">
                                Prevent breaking double lesson
                            </div>
                            <div class="col-4">
                                <select class="form-control select" v-model="double" @change="update_filter_swap('double_prevent')">
                                    <option value='enable'>Enable</option>
                                    <option value='false'>Disable</option>
                                </select>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                No lesson for certain class during a week/cycle
                            </div>
                            <div class="col-4">
                                <select class="form-control select" v-model="cycle_empty" @change="update_filter_swap('empty_cycle')">
                                    <option value='enable'>Enable</option>
                                    <option value='false'>Disable</option>
                                </select>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                Maximum session difference
                            </div>
                            <div class="col-4">
                               <input type='number' step='1' v-model="session_offset" class="form-control" @change="update_filter_swap('session_diff')"/>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                No lesson for certain day
                            </div>
                            <div class="col-4">
                                <select class="form-control select" v-model="day_no_lesson" @change="update_filter_swap('single_lesson')">
                                    <option value='enable'>Enable</option>
                                    <option value='false'>Disable</option>
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-6">
                <!--Sub Filter-->
                <div class="card">
                    <div class="card-header">
                        <strong>Substitude Filter Setting(s)</strong>
                    </div>    
                    <div class="card-body">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            filter:[],
            ready:false,
            double:'false',
            cycle_empty:'false',
            session_offset:0,
            day_no_lesson:'false',
        }
    },
    async created(){
        await this.fetch_default();
        this.ready = true;
    },
    methods:{
        async fetch_default(){
            await axios.get('subsitude/admin/backend/fetch_filters.php')
                .then(
                    (response)=>{
                        this.filter = response.data;
                        var i = 0;
                        for(i=0;i<this.filter.length;i++){
                            switch(this.filter[i].name){
                                case 'double_prevent':
                                    this.double = this.filter[i].status;
                                break;
                                case 'empty_cycle':
                                    this.cycle_empty = this.filter[i].status;
                                break;
                                case 'session_diff':
                                    this.session_offset = this.filter[i].offset;
                                break;
                                case 'single_lesson':
                                    this.day_no_lesson = this.filter[i].status;
                                break;
                            }
                        }
                    }
                )
        },
        update_filter_swap(type){
            var formData = new FormData();
            formData.append('type','swap');
            formData.append('filter',type);
            switch (type){
                case 'double_prevent':
                    formData.append('status',this.double);
                break;
                case 'empty_cycle':
                    formData.append('status',this.cycle_empty);
                break;
                case 'session_diff':
                    formData.append('offset',this.session_offset);
                    if(this.session_offset==0){                  
                        formData.append('status','false');
                    } else {
                        formData.append('status','enable')
                    }
                break;
                case 'single_lesson':
                    formData.append('status',this.day_no_lesson);
                break;
            }
            axios.post('subsitude/admin/backend/update_filter.php',formData)
                .then(
                    (response)=>{

                    }
                )
        }
    }
}
</script>
<style scoped>
    .row{
        margin-top:14px;
    }
</style>


